@import "./poems";
@import "./poemWheel";

html {
    background-color: #060607;
}

button {
    padding: .5rem;
    width: 10rem;
    margin: 0rem;
    background-color: #060607;
    border-radius: 1rem;
    border-color: white;
    color: white;
    &.selected {
        border-color: greenyellow;
         color: greenyellow;
    }
}

.row {
    display: flex;
    flex-direction: row;
}

.col {
    width: 50%;
    display: flex;
    flex-direction: column;
}