.poem {
  font-family: "Calibri", sans-serif;
  color: rgb(225, 225, 225);
  text-align: center;
  margin: auto;
  width: -moz-fit-content;
  width: fit-content;
  min-height: 400px;
  margin-bottom: 5rem;
}
.poem .text-wrapper p {
  font-size: 18px;
  text-align: left;
  font-weight: 100;
  margin-top: 0;
  margin-bottom: 0.5rem;
  margin-left: 0.75rem;
}
.poem .text-wrapper h1 {
  font-size: 36px;
  text-align: left;
  margin-bottom: 2rem;
}

.PoemWheel {
  max-height: 200px;
  overflow-y: auto;
  margin: auto;
  padding: 0.5rem;
  width: 75%;
  border-style: solid;
  border-color: white;
  border-left: none;
  border-right: none;
  scrollbar-color: white transparent;
  scrollbar-width: none;
  scroll-snap-align: center;
}
.PoemWheel button {
  margin: auto;
  margin-top: 2rem;
  width: 75%;
  font-size: 14px;
  background-color: #060607;
  border-radius: 0.25rem;
  border-color: white;
  border-left: none;
  border-right: none;
  border-top: none;
  color: white;
}
.PoemWheel button.selected {
  border-color: greenyellow;
  color: greenyellow;
}

html {
  background-color: #060607;
}

button {
  padding: 0.5rem;
  width: 10rem;
  margin: 0rem;
  background-color: #060607;
  border-radius: 1rem;
  border-color: white;
  color: white;
}
button.selected {
  border-color: greenyellow;
  color: greenyellow;
}

.row {
  display: flex;
  flex-direction: row;
}

.col {
  width: 50%;
  display: flex;
  flex-direction: column;
}/*# sourceMappingURL=index.css.map */